<template>
  <modal v-if="!name" @set-name="setName" />
  <div class="--dark-theme" id="chat">
    <chat v-if="name" :init-name="name" />
  </div>
</template>

<script>
import Modal from "@/components/InitModal.vue";
import Chat from "@/components/Chat.vue";

export default {
  name: "App",
  components: {
    Modal,
    Chat,
  },
  data() {
    return {
      name: null,
    };
  },
  methods: {
    setName(name) {
      this.name = name;
    },
  },
};
</script>
