import { v4 as uuidV4 } from "uuid";

import { db } from "@/main";
import { ref, push, onValue } from "firebase/database";

export default {
  name: "ChatGroup",
  props: {
    initName: String,
  },
  data() {
    return {
      uid: uuidV4(),
      name: null,
      message: null,
      // messages
      messages: [],
      messageKeys: [],
      // scroll
      init: false,
      boardAtBottom: true,
    };
  },
  created() {
    this.name = this.initName.substring(0, 15).trim();
    // watch
    onValue(ref(db, "messages"), (snapshot) => {
      const data = snapshot.val();
      this.updateMessages(data);
    });
  },
  mounted() {
    this.$refs.inputMessage.focus();
    this.$refs.chatBoard.addEventListener("scroll", () => {
      this.boardAtBottom =
        this.$refs.chatBoard.scrollTop >
        this.$refs.chatBoard.scrollHeight -
          this.$refs.chatBoard.offsetHeight -
          15;
    });
  },
  methods: {
    updateMessages(data) {
      if (!data) {
        return;
      }
      // data keys
      const keys = Object.keys(data);
      let newKeys = [];
      for (let i = keys.length - 1; i >= 0; i--) {
        // check key
        const key = keys[i];
        if (this.messageKeys.includes(key)) {
          break;
        }
        // push data
        this.messageKeys.push(key);
        newKeys.push(key);
      }
      // for each key
      newKeys.reverse();
      for (let i = 0; i < newKeys.length; i++) {
        const key = newKeys[i];
        const lastIndex = this.messages.length - 1;
        // multiple messages
        if (
          this.messages[lastIndex] &&
          data[key].uid === this.messages[lastIndex].uid
        ) {
          this.messages[lastIndex].messages.push(data[key].message);
          continue;
        }
        // single message
        this.messages.push({
          uid: data[key].uid,
          name: data[key].name,
          messages: [data[key].message],
        });
      }
      this.boardScroll();
    },
    messageOnEnter() {
      // check message
      if (!this.message.trim()) {
        return;
      }
      // set
      // set(ref(db, "users/" + 1), {
      //   name: this.name,
      // });
      // push message
      push(ref(db, "messages"), {
        uid: this.uid,
        name: this.name,
        message: this.message,
      }).then();
      // clear message
      this.message = null;
      // force
      this.boardScroll(true);
    },
    boardScroll(force = false) {
      this.$nextTick(() => {
        if (!this.init) {
          this.init = true;
          this.boardScrollToBottom();
          return;
        }
        if (force) {
          this.boardScrollToBottom(true);
          return;
        }
        if (!this.boardAtBottom) {
          return;
        }
        this.boardScrollToBottom();
      }).then();
    },
    boardScrollToBottom(smooth = false) {
      this.$refs.chatBoard.scrollTo({
        top: this.$refs.chatBoard.scrollHeight,
        behavior: smooth ? "smooth" : "instant",
      });
    },
  },
};
